(function() {
'use strict';

angular
    .module('front')
    .controller('ViewTestCaseController', ViewTestCaseController);

/** @ngInject */
function ViewTestCaseController($routeParams, testCaseService, routeContest, routeProblem, routeTestCase) {
    var vm = this;

    vm.init = function() {
        vm.contest = routeContest;
        vm.problem = routeProblem;
        vm.test_case = routeTestCase;

        vm.contest_nickname = $routeParams.contest_nickname;
        vm.problem_nickname = $routeParams.problem_nickname;
        vm.test_case_id = $routeParams.test_case_id;
    };

    vm.loadMore = function(inputType) {
        var query = {};
        query['complete_' + inputType] = 'true';

        vm[inputType + 'Loading'] = true;
        testCaseService.getTestCase(
            vm.contest_nickname,
            vm.problem_nickname,
            vm.test_case_id,
            query
        )
        .then(function(test_case) {
            vm.test_case.current[inputType] = test_case.current[inputType];
            vm[inputType + 'Loaded'] = true;
            vm[inputType + 'Loading'] = false;
        })
    };

    vm.init();
    return vm;
}
})();

(function() {
'use strict';

angular
    .module('front')
    .controller('EditTestCaseController', EditTestCaseController);

/** @ngInject */
function EditTestCaseController($scope, $location, $filter, routeContest, routeProblem, routeTestCase, testCaseService) {
    var vm = this;

    vm.init = function() {
        vm.contest  = routeContest;
        vm.problem  = routeProblem;
        vm.testCase = routeTestCase;

        fillInitialValues();

        vm.currentUploadingCount = 0;
    };

    function fillInitialValues() {
        vm.form = {
            input:  vm.testCase.current.sample_input + (vm.testCase.current.large_input ? '...' : ''),
            output: vm.testCase.current.sample_output + (vm.testCase.current.large_output ? '...' : '')
        };

        if(vm.testCase.current.input_file_name) {
            vm.form.inputFile = {
                original: true,
                name:     vm.testCase.current.input_file_name
            };
        }
        if(vm.testCase.current.output_file_name) {
            vm.form.outputFile = {
                original: true,
                name:     vm.testCase.current.output_file_name
            };
        }
    }

    vm.submit = function(form) {
        var params = {};

        if(form.inputFile) {
            if(form.inputSignedUrl) {
                params.input_file_name = form.inputSignedUrl.file_name;
            } else {
                params.input_file_name = form.inputFile.name;
            }
        } else {
            params.input = form.input;
        }

        if(form.outputFile) {
            if(form.outputSignedUrl) {
                params.output_file_name = form.outputSignedUrl.file_name;
            } else {
                params.output_file_name = form.outputFile.name;
            }
        } else {
            params.output = form.output;
        }

        testCaseService.editTestCase(vm.contest.nickname, vm.problem.nickname, vm.testCase._id, params)
        .then(function(test_case) {
            $location.path($filter('url')(
                'contest.problem.testCase.view', 
                vm.contest.nickname, 
                vm.problem.nickname, 
                test_case._id
            ));
        });
    };


    vm.loadTestCaseFromFile = function(fileType, file) {
        if(!file) {
            return;
        }

        var fileReader = new FileReader();
        fileReader.onload = function(evt) {
            $scope.$apply(function () {
                if(fileReader.result.length > 500) {
                    vm.form[fileType] = fileReader.result.substr(0, 497) + '...';
                } else {
                    vm.form[fileType] = fileReader.result;
                }
            });
        };
        fileReader.onerror = function(evt) {
        };
        fileReader.readAsText(file);
    };

    vm.getSignedDownloadCallback = function(fileType) {
        return testCaseService.getDownloadFileSignedUrl(
            vm.contest.nickname, 
            vm.problem.nickname, 
            vm.form.inputFile.name
        );
    };

    vm.getSignedUploadCallback = function(fileType, file) {
        return testCaseService.getUploadFileSignedUrl(vm.contest.nickname, vm.problem.nickname, {})
            .then(function(signedUrl) {
                vm.loadTestCaseFromFile(fileType, file);

                vm.form[fileType + 'File'] = file;
                vm.form[fileType + 'SignedUrl'] = signedUrl;
                vm.currentUploadingCount++;

                return signedUrl;
            });
    };

    vm.afterUploadCallback = function(fileType, file) {
        vm.currentUploadingCount--;

        return Promise.resolve();
    };

    vm.removeCallback = function(fileType) {
        if(vm.form[fileType + 'File'].original) {
            delete vm.form[fileType + 'File'];
            vm.form[fileType] = '';

            return Promise.resolve();
        }

        return testCaseService.deleteFile(
            vm.contest.nickname, 
            vm.problem.nickname, 
            vm.form[fileType + 'SignedUrl'].file_name
        )
        .then(function(data) {
            delete vm.form[fileType + 'File'];
            delete vm.form[fileType + 'SignedUrl'];
            vm.form[fileType] = '';

            return data;
        });
    };

    vm.init();
    return vm;
}
})();

(function() {
'use strict';

angular
    .module('front')
    .controller('CreateTestCaseController', CreateTestCaseController);

/** @ngInject */
function CreateTestCaseController($scope, $location, $filter, Upload, PromiseProcess, routeContest, routeProblem, testCaseService) {
    var vm = this;

    vm.init = function() {
        vm.contest = routeContest;
        vm.problem = routeProblem;

        vm.form = {
            test_cases: [{
                input:  '',
                output: ''
            }]
        };

        vm.currentUploadingCount = 0;
    };

    vm.addTestCase = function() {
        vm.form.test_cases.push({
            input:  '',
            output: ''
        });
    };

    vm.removeTestCase = function(index) {
        vm.form.test_cases.splice(index, 1);
    };

    vm.submit = function(form) {
        var promiseProcess = PromiseProcess();

        form.test_cases.forEach(function(test_case) {
            promiseProcess.push(function() {
                var params = {};

                if(test_case.inputSignedUrl) {
                    params.input_file_name = test_case.inputSignedUrl.file_name;
                } else {
                    params.input = test_case.input;
                }

                if(test_case.outputSignedUrl) {
                    params.output_file_name = test_case.outputSignedUrl.file_name;
                } else {
                    params.output = test_case.output;
                }

                return testCaseService.createTestCase(vm.contest.nickname, vm.problem.nickname, params)
                .then(function(test_case) {
                    return test_case._id;
                });
            });
        });

        promiseProcess.exec([])
        .then(function(test_case_id) {
            $scope.$apply(function() {
                if(form.test_cases.length == 1) {
                    $location.path($filter('url')(
                        'contest.problem.testCase.view', 
                        vm.contest.nickname, 
                        vm.problem.nickname, 
                        test_case_id
                    ));
                } else {
                    $location.path($filter('url')(
                        'contest.problem.view', 
                        vm.contest.nickname, 
                        vm.problem.nickname
                    ));
                }
            });
        });
    };


    vm.loadTestCaseFromFile = function(index, fileType, file) {
        if(!file) {
            return;
        }

        var fileReader = new FileReader();
        fileReader.onload = function(evt) {
            $scope.$apply(function () {
                if(fileReader.result.length > 500) {
                    vm.form.test_cases[index][fileType] = fileReader.result.substr(0, 497) + '...';
                } else {
                    vm.form.test_cases[index][fileType] = fileReader.result;
                }
            });
        };
        fileReader.onerror = function(evt) {
        };
        fileReader.readAsText(file);
    };

    vm.getSignedDownloadCallback = function(index, fileType) {
        return testCaseService.getDownloadFileSignedUrl(
            vm.contest.nickname, 
            vm.problem.nickname, 
            vm.form.test_cases[index][fileType + 'SignedUrl'].file_name
        );
    };

    vm.getSignedUploadCallback = function(index, fileType, file) {
        return testCaseService.getUploadFileSignedUrl(vm.contest.nickname, vm.problem.nickname, {})
            .then(function(signedUrl) {
                vm.loadTestCaseFromFile(index, fileType, file);

                vm.form.test_cases[index][fileType + 'File'] = file;
                vm.form.test_cases[index][fileType + 'SignedUrl'] = signedUrl;
                vm.currentUploadingCount++;

                return signedUrl;
            });
    };

    vm.afterUploadCallback = function(index, fileType, file) {
        vm.currentUploadingCount--;

        return Promise.resolve();
    };

    vm.removeCallback = function(index, fileType) {
        return testCaseService.deleteFile(
            vm.contest.nickname, 
            vm.problem.nickname, 
            vm.form.test_cases[index][fileType + 'SignedUrl'].file_name
        )
        .then(function(data) {
            delete vm.form.test_cases[index][fileType + 'File'];
            delete vm.form.test_cases[index][fileType + 'SignedUrl'];
            vm.form.test_cases[index][fileType] = '';

            return data;
        });
    };


    vm.init();
    return vm;
}
})();

