(function() {
'use strict';

angular
  .module('front')
  .directive('singleAttachment', singleAttachment);

/** @ngInject */
function singleAttachment() {
    var directive = {
        restrict        : 'E',
        templateUrl     : 'app/components/single-attachment/single-attachment.html',
        scope           : {
            // the file object (must have a name property).
            file: '<',

            // text to show on 'attach' button.
            customAttachText: '<',

            // callbacks
            signedDownloadCallback: '&',
            signedUploadCallback:   '&',
            afterUploadCallback:    '&',
            removeCallback:         '&'
        },
        controller      : SingleAttachmentController,
        controllerAs    : 'vm',
        bindToController: true
    };

    return directive;

    /** @ngInject */
    function SingleAttachmentController(downloadService, Upload) {
        var vm = this;

        vm.init = function() {
            vm.loading = false;
        };

        vm.downloadFile = function() {
            vm.loading = true;

            vm.signedDownloadCallback()
                .then(function(signedUrl) {
                    vm.loading = false;

                    downloadService.download(signedUrl, false, vm.file.name);
                })
                .catch(function(err) {
                    vm.loading = false;

                    vm.err = 'No foi possvel baixar este arquivo.';
                });
        };

        vm.removeFile = function() {
            vm.loading = true;

            vm.removeCallback()
                .then(function(response) {
                    vm.loading = false;

                    vm.file = undefined;
                })
                .catch(function(err) {
                    vm.loading = false;

                    vm.err = 'No foi possvel excluir este arquivo.';
                });
        };

        vm.uploadFile = function(file, errFiles) {
            if(errFiles && errFiles[0]) {
                errFiles = errFiles[0];

                if(errFiles.$errorMessages && errFiles.$errorMessages.maxSize) {
                    return vm.err = 'O arquivo selecionado ultrapassa o limite de 4MB.';
                }

                return vm.err = 'Erro desconhecido.';
            }

            if(!file) {
                return;
            }
            var fileName = file.name;

            vm.loading = true;
            vm.signedUploadCallback({ file: file })
                .then(function(signedUrl) {
                    return new Promise(function(resolve, reject) {
                        Upload.upload({
                            url:    signedUrl.url,
                            method: 'POST',
                            data:   {
                                key:                signedUrl.key,
                                Policy:             signedUrl.fields.Policy,
                                'X-Amz-Algorithm':  signedUrl.fields['X-Amz-Algorithm'],
                                'X-Amz-Credential': signedUrl.fields['X-Amz-Credential'],
                                'X-Amz-Date':       signedUrl.fields['X-Amz-Date'],
                                'X-Amz-Signature':  signedUrl.fields['X-Amz-Signature'],
                                file:               file
                            },
                            headers: {
                                'x-access-token': undefined
                            }
                        })
                        .then(function finishCallback(response) {
                            return resolve(response.data);
                        }, function errCallback(err) {
                            return reject(err);
                        }, function progressCallback(evt) {
                            file.progress = Math.min(100, parseInt(100.0 * evt.loaded / evt.total));
                        });
                    });
                })
                .then(function(result) {
                    return vm.afterUploadCallback({ file: file });
                })
                .then(function(response) {
                    vm.loading = false;

                    vm.file = {
                        name: fileName
                    };
                })
                .catch(function(err) {
                    vm.loading = false;

                    vm.err = 'No foi possvel fazer upload deste arquivo.';
                });
        };

        vm.init();
        return vm;
    }
}

})();

(function() {
'use strict';

angular
  .module('front')
  .directive('runsList', runsList);

/** @ngInject */
function runsList() {
    var directive = {
        restrict        : 'E',
        templateUrl     : 'app/components/runs-list/runs-list.html',
        scope           : {
            runs:    '<',
            getLink: '<'
        },
        controller      : RunsListController,
        controllerAs    : 'vm',
        bindToController: true
    };

    return directive;

    /** @ngInject */
    function RunsListController(utilService) {
        var vm = this;

        vm.init = function() {
            vm.solutionRuns = [];
            for(var i=0; i<vm.runs.length; i++) {
                var newSolutionRun = {
                    run_number: vm.runs[i].run_number,
                    runs:       [ vm.runs[i] ],
                    timestamp:  vm.runs[i].timestamp
                };

                for(var j=vm.runs.length-1; j>i; j--) {
                    if(vm.runs[i].run_number === vm.runs[j].run_number) {
                        newSolutionRun.runs.push(vm.runs[j]);
                        vm.runs.splice(j, 1);
                    }
                }

                vm.solutionRuns.push(newSolutionRun);
            }
            vm.solutionRuns.sort(function(a, b) {
                return b.run_number - a.run_number;
            });
        };

        vm.init();
        return vm;
    }
}

})();

(function() {
'use strict';

angular
  .module('front')
  .directive('runResultTable', runResultTable);

/** @ngInject */
function runResultTable() {
    var directive = {
        restrict        : 'E',
        templateUrl     : 'app/components/run-result-table/run-result-table.html',
        scope           : {
            problem:   '<',
            solutions: '<',
            checkers:  '<',
            testCases: '<',
            runNumber: '<',

            customExpectedOutput: '<'
        },
        controller      : RunResultTableController,
        controllerAs    : 'vm',
        bindToController: true
    };

    return directive;

    /** @ngInject */
    function RunResultTableController(utilService) {
        var vm = this;

        vm.init = function() {
            // if not defined which solutions should be shown, 
            // it will show all the solutions that were run.
            if(typeof vm.solutions === 'boolean' && vm.solutions) {
                vm.items = vm.problem.solutions.filter(function(solution) {
                    return solution.run.some(function(solutionRun) {
                        return solutionRun.run_number == vm.runNumber;
                    });
                });
            } else if(Array.isArray(vm.solutions)) {
                vm.items = vm.solutions;
            }

            // if not defined which checkers should be shown, 
            // it will show all the checkers that were run.
            if(typeof vm.checkers === 'boolean' && vm.checkers) {
                vm.items = vm.problem.checkers.filter(function(solution) {
                    return solution.run.some(function(solutionRun) {
                        return solutionRun.run_number == vm.runNumber;
                    });
                });
            } else if(Array.isArray(vm.checkers)) {
                vm.items = vm.checkers;
            }

            // all the runs with the run_number.
            vm.runs = vm.items.reduce(function(prev, item) {
                return prev.concat(
                    item.run
                    .filter(function(itemRun) {
                        return itemRun.run_number == vm.runNumber;
                    })
                    .map(function(itemRun) {
                        itemRun.item              = item;
                        itemRun.test_case         = getTestCase(vm.problem, itemRun.test_case_id);
                        itemRun.test_case.current = itemRun.test_case.v[ itemRun.test_case.v.length-1 ];

                        return itemRun;
                    })
                );
            }, []);

            // all the test_cases that were tested.
            vm.testCases = vm.runs.reduce(function(prev, solutionRun) {
                if(prev.indexOf(solutionRun.test_case) === -1) {
                    prev.push(solutionRun.test_case);
                }
                return prev;
            }, []);

            // map with all the item/test_case results.
            vm.testCaseRunMap = {};
        };

        function getTestCase(problem, test_case_id) {
            return problem.test_cases.filter(function(test_case) {
                return test_case._id == test_case_id;
            })[0];
        }

        vm.selectRun = function(runs, item_id, test_case_id) {
            vm.selectedRun = vm.getTestCaseRun(runs, item_id, test_case_id);
        };

        vm.unselectRun = function() {
            vm.selectedRun = null;
        };

        vm.getTestCaseRun = function(runs, item_id, test_case_id) {
            if(vm.testCaseRunMap[item_id + '-' + test_case_id] === undefined) {
                var testCaseRun = runs.filter(function(run) {
                    return run.item._id  == item_id
                        && run.test_case._id == test_case_id;
                })[0];

                testCaseRun.verdict = utilService.getVerdict(testCaseRun.verdict);

                vm.testCaseRunMap[item_id + '-' + test_case_id] = testCaseRun;
            }

            return vm.testCaseRunMap[item_id + '-' + test_case_id];
        };

        vm.init();
        return vm;
    }
}

})();

