(function() {
'use strict';

angular
	.module('front')
	.directive('ngTransclude', customNgTransclude);

/** @ngInject */
function customNgTransclude() {
    return {
        restrict: 'EAC',
        link: function($scope, $element, $attrs, controller, $transclude) {
            if(!$transclude) {
                throw minErr('ngTransclude')(
					'orphan',
					'Illegal use of ngTransclude directive in the template! ' +
					'No parent directive that requires a transclusion found. ' +
					'Element: {0}',
					startingTag($element)
				);
            }

            var iScopeType = $attrs['ngTransclude'] || 'sibling';

            switch(iScopeType) {
                case 'sibling':
                    $transclude(function(clone) {
                        $element.append(clone);
                    });
                    break;

                case 'parent':
                    $transclude($scope, function(clone) {
                        if(clone.length) {
                            $element.append(clone);
                        }
                    });
                    break;

                case 'child':
                    var iChildScope = $scope.$new();
                    $transclude(iChildScope, function(clone) {
                        $element.append(clone);
                        $element.on('$destroy', function() {
                            iChildScope.$destroy();
                        });
                    });
                    break;
            }
        }
    }
}

})();

(function() {
'use strict';

angular
	.module('front')
	.filter('url', url)
	.filter('myDate', myDate);

/** @ngInject */
function url(urlService) {
	return function(input) {
		var args = [];
		for(var i=1; i<arguments.length; i++) {
			args.push(arguments[i]);
		}

		return urlService.getUrl(input, args);
	};
}

/** @ngInject */
function myDate() {
	return function(input) {
		var currentDate = moment(),
			inputDate   = moment(input);

		if(inputDate.isBefore(currentDate)) {
			var diffInMinutes = currentDate.diff(inputDate, 'minutes', true),
				diffInHours   = currentDate.diff(inputDate, 'hours', true),
				diffInDays    = currentDate.diff(inputDate, 'days', true),
				diffInMonths  = currentDate.diff(inputDate, 'months', true);

			if(diffInMinutes <= 1) {
				return 'agora';
			} else if(diffInHours < 1) {
				return ' ' + Math.floor(diffInMinutes) + ' minuto' + (diffInMinutes >= 2 ? 's' : '');
			} else if(diffInDays < 1) {
				return ' ' + Math.floor(diffInHours) + ' hora' + (diffInHours >= 2 ? 's' : '');
			} else if(diffInMonths < 1) {
				return ' ' + Math.floor(diffInDays) + ' dia' + (diffInDays >= 2 ? 's' : '');
			} else {
				return ' ' + Math.floor(diffInMonths) + ' mes' + (diffInMonths >= 2 ? 's' : '');
			}
		} else {
		}
	}
}

})();

